('/*-- Chart --*/\r\n.c3 svg {\r\n  font: 10px sans-serif;\r\n  -webkit-tap-highlight-color: transparent; }\r\n\r\n.c3 path, .c3 line {\r\n  fill: none;\r\n  stroke: #000; }\r\n\r\n.c3 text {\r\n  -webkit-user-select: none;\r\n  -moz-user-select: none;\r\n  user-select: none; }\r\n\r\n.c3-legend-item-tile,\r\n.c3-xgrid-focus,\r\n.c3-ygrid,\r\n.c3-event-rect,\r\n.c3-bars path {\r\n  shape-rendering: crispEdges; }\r\n\r\n.c3-chart-arc path {\r\n  stroke: #fff; }\r\n\r\n.c3-chart-arc text {\r\n  fill: #fff;\r\n  font-size: 13px; }\r\n\r\n/*-- Axis --*/\r\n/*-- Grid --*/\r\n.c3-grid line {\r\n  stroke: #aaa; }\r\n\r\n.c3-grid text {\r\n  fill: #aaa; }\r\n\r\n.c3-xgrid, .c3-ygrid {\r\n  stroke-dasharray: 3 3; }\r\n\r\n/*-- Text on Chart --*/\r\n.c3-text.c3-empty {\r\n  fill: #808080;\r\n  font-size: 2em; }\r\n\r\n/*-- Line --*/\r\n.c3-line {\r\n  stroke-width: 1px; }\r\n\r\n/*-- Point --*/\r\n.c3-circle._expanded_ {\r\n  stroke-width: 1px;\r\n  stroke: white; }\r\n\r\n.c3-selected-circle {\r\n  fill: white;\r\n  stroke-width: 2px; }\r\n\r\n/*-- Bar --*/\r\n.c3-bar {\r\n  stroke-width: 0; }\r\n\r\n.c3-bar._expanded_ {\r\n  fill-opacity: 0.75; }\r\n\r\n/*-- Focus --*/\r\n.c3-target.c3-focused {\r\n  opacity: 1; }\r\n\r\n.c3-target.c3-focused path.c3-line, .c3-target.c3-focused path.c3-step {\r\n  stroke-width: 2px; }\r\n\r\n.c3-target.c3-defocused {\r\n  opacity: 0.3 !important; }\r\n\r\n/*-- Region --*/\r\n.c3-region {\r\n  fill: steelblue;\r\n  fill-opacity: .1; }\r\n\r\n/*-- Brush --*/\r\n.c3-brush .extent {\r\n  fill-opacity: .1; }\r\n\r\n/*-- Select - Drag --*/\r\n/*-- Legend --*/\r\n.c3-legend-item {\r\n  font-size: 12px; }\r\n\r\n.c3-legend-item-hidden {\r\n  opacity: 0.15; }\r\n\r\n.c3-legend-background {\r\n  opacity: 0.75;\r\n  fill: white;\r\n  stroke: lightgray;\r\n  stroke-width: 1; }\r\n\r\n/*-- Title --*/\r\n.c3-title {\r\n  font: 14px sans-serif; }\r\n\r\n/*-- Tooltip --*/\r\n.c3-tooltip-container {\r\n  z-index: 10; }\r\n\r\n.c3-tooltip {\r\n  border-collapse: collapse;\r\n  border-spacing: 0;\r\n  background-color: #fff;\r\n  empty-cells: show;\r\n  -webkit-box-shadow: 7px 7px 12px -9px #777777;\r\n  -moz-box-shadow: 7px 7px 12px -9px #777777;\r\n  box-shadow: 7px 7px 12px -9px #777777;\r\n  opacity: 0.9; }\r\n\r\n.c3-tooltip tr {\r\n  border: 1px solid #CCC; }\r\n\r\n.c3-tooltip th {\r\n  background-color: #aaa;\r\n  font-size: 14px;\r\n  padding: 2px 5px;\r\n  text-align: left;\r\n  color: #FFF; }\r\n\r\n.c3-tooltip td {\r\n  font-size: 13px;\r\n  padding: 3px 6px;\r\n  background-color: #fff;\r\n  border-left: 1px dotted #999; }\r\n\r\n.c3-tooltip td > span {\r\n  display: inline-block;\r\n  width: 10px;\r\n  height: 10px;\r\n  margin-right: 6px; }\r\n\r\n.c3-tooltip td.value {\r\n  text-align: right; }\r\n\r\n/*-- Area --*/\r\n.c3-area {\r\n  stroke-width: 0;\r\n  opacity: 0.2; }\r\n\r\n/*-- Arc --*/\r\n.c3-chart-arcs-title {\r\n  dominant-baseline: middle;\r\n  font-size: 1.3em; }\r\n\r\n.c3-chart-arcs .c3-chart-arcs-background {\r\n  fill: #e0e0e0;\r\n  stroke: none; }\r\n\r\n.c3-chart-arcs .c3-chart-arcs-gauge-unit {\r\n  fill: #000;\r\n  font-size: 16px; }\r\n\r\n.c3-chart-arcs .c3-chart-arcs-gauge-max {\r\n  fill: #777; }\r\n\r\n.c3-chart-arcs .c3-chart-arcs-gauge-min {\r\n  fill: #777; }\r\n\r\n.c3-chart-arc .c3-gauge-value {\r\n  fill: #000;\r\n  /*  font-size: 28px !important;*/ }\r\n');
